<template>

  <div id="fh5co-main-content">
     <div class="fh5co-post">
        <div v-highlight class="fh5co-entry padding" style="width:auto;" v-html="compiledMarkdown"> </div>
        <vueToTop type="6" color="#F79420"></vueToTop>
        <v-footer></v-footer>
      </div>
    </div>
  </div>


</template>

<script>
import { get } from '@/api/article'
import marked from 'marked'
import vFooter from './common/Footer.vue';


 marked.setOptions({
    renderer: new marked.Renderer(),
    gfm: true,
    tables: true,
    breaks: false,
    pedantic: false,
    sanitize: false,
    smartLists: true,
    smartypants: false,
    highlight: function (code, lang) {
        /*if (lang && hljs.getLanguage(lang)) {
            return hljs.highlight(lang, code, true).value;
          } else {
            return hljs.highlightAuto(code).value;
          }*/

        return code
      }
  });


export default {
  name: 'detail',
  data () {
    return {
      html: ''
    }
  },
  components:{
     vFooter
  },
  watch:{
    $route: {
      handler: function(to, from) {
         if(to.name === from.name) {
            this.reload()
         }
      },
      deep: true
    }
  },
  created() {
    this.reload()
  },
  methods: {
    reload(){
      var id = this.$route.params.id; // 文章唯一标识
      get(id).then(response => {
        this.html = response; //.replace(/\n/g,"\\n").replace(/\r/g,"\\r");
      })
    }
  },
  computed: {
    compiledMarkdown() {
      return marked(this.html || '', {
        sanitize: true
      });
    }
  }
}
</script>

<style scoped>

.fh5co-entry{
  float: none;
}

</style>



